.contact-container {
    text-align: center;
    /* background-color: #3CB371; */
    background-color: #1f1f38;;
    padding: 1rem;
    padding-bottom: 3rem;
}

.contact-title {
    color: pink;
    font-size: 3.5rem;
}

.contact-content {
    display: flex;
    /* flex-direction: column; */
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
}

.contact-link {
    display: flex;
    align-items: center;
    gap: 0.2rem;
    font-size: 1.3rem;
    justify-content: center;
    color: white;
    text-decoration: none;
    background-color: #2c2c6c;
    width: 15rem;
    margin-bottom: 1rem;
    padding: 0.5rem;
    border-radius: 1rem;
    transition: 1s;
    box-shadow: 4px 10px 10px #2e2e48e3;
    margin-left: 1rem;
}

.contact-link:hover {
    background-color: pink;
    padding: 1rem;
}

@media screen and (max-width: 505px) { 
    .contact-link { 
        width: 12rem;
        font-size: 1rem;
    }
}